import React, { useState } from "react";
import {
	StyledMovieLeftTopDetails,
	StyledMovieRating,
	StyledMovieRatingFeedback,
} from "./styles/MovieLeftTopDetails.styled";

import Rating from "@mui/material/Rating";

import RateReviewIcon from "@mui/icons-material/RateReview";

const MovieLeftTopDetails = () => {
	const [value, setValue] = useState<number>(0);

	return (
		<StyledMovieLeftTopDetails>
			<img
				src="data:image/jpeg;base64,"
				alt="Lalaland movie"
			/>
			<StyledMovieRating>
				<p>
					<RateReviewIcon />
					<span>Rate movie:</span>
				</p>
				<div>
					<Rating
						name="half-rating-read"
						precision={0.1}
						size="large"
						value={value}
						onChange={(_event, newValue) => {
							setValue(newValue || 0);
						}}
					/>
					{value > 0 && <span className="value">{value.toFixed(1)}</span>}
				</div>
				<StyledMovieRatingFeedback>
					{value ? "Thanks for you feedback!" : ""}
				</StyledMovieRatingFeedback>
			</StyledMovieRating>
		</StyledMovieLeftTopDetails>
	);
};

export default MovieLeftTopDetails;
